/* Variables */
.avatar-uploader .el-upload {
  border-radius: 4px !important;
  background: #FCFCFC;
}
.avatar-uploader .avatar-uploader-icon {
  background: #FCFCFC;
}
.avatar-uploader .el-icon-plus:before {
  content: "Upload Picture" !important;
  font-size: 12px;
  color: #000;
}
.el-dialog {
  border-radius: 8px;
}
.el-dialog__header {
  background: #FBFBFA;
  border-radius: 8px 8px 0 0;
  border: none;
}

/* Mixins */
.navbar .pwdCon .el-dialog__body {
  padding-top: 60px;
  padding: 60px 100px 0;
}
.navbar .pwdCon .el-input__inner {
  padding: 0 12px;
}
.navbar .pwdCon .el-form-item {
  margin-bottom: 26px;
}
.navbar .pwdCon .el-form-item__label {
  text-align: left;
}
.navbar .pwdCon .el-dialog__footer {
  padding-top: 14px;
}