/* Variables */
.avatar-uploader .el-upload {
  border-radius: 4px !important;
  background: #FCFCFC;
}
.avatar-uploader .avatar-uploader-icon {
  background: #FCFCFC;
}
.avatar-uploader .el-icon-plus:before {
  content: "Upload Picture" !important;
  font-size: 12px;
  color: #000;
}
.el-dialog {
  border-radius: 8px;
}
.el-dialog__header {
  background: #FBFBFA;
  border-radius: 8px 8px 0 0;
  border: none;
}

/* Mixins */
.addDish .el-checkbox__label {
  width: 100%;
}
.addDish .empty-box {
  margin-top: 50px;
  margin-bottom: 0px;
}