/* Variables */
.avatar-uploader .el-upload {
  border-radius: 4px !important;
  background: #FCFCFC;
}
.avatar-uploader .avatar-uploader-icon {
  background: #FCFCFC;
}
.avatar-uploader .el-icon-plus:before {
  content: "Upload Picture" !important;
  font-size: 12px;
  color: #000;
}
.el-dialog {
  border-radius: 8px;
}
.el-dialog__header {
  background: #FBFBFA;
  border-radius: 8px 8px 0 0;
  border: none;
}

/* Mixins */
.el-notification {
  width: 419px !important;
}
.el-notification .el-notification__title {
  margin-bottom: 14px;
  color: #333;
}
.el-notification .el-notification__title .el-notification__content {
  color: #333;
}
.navbar .el-dialog {
  min-width: auto !important;
}
.navbar .el-dialog__header {
  height: 61px;
  line-height: 60px;
  background: #fbfbfa;
  padding: 0 30px;
  font-size: 16px;
  color: #333;
  border: 0 none;
}
.navbar .el-dialog__body {
  padding: 10px 30px 30px;
}
.navbar .el-dialog__body .el-radio,
.navbar .el-dialog__body .el-radio__input {
  white-space: normal;
}
.navbar .el-dialog__body .el-radio__label {
  padding-left: 5px;
  color: #333;
  font-weight: 700;
}
.navbar .el-dialog__body .el-radio__label span {
  display: block;
  line-height: 20px;
  padding-top: 12px;
  color: #666;
  font-weight: normal;
}
.navbar .el-dialog__body .el-radio__input.is-checked .el-radio__inner::after {
  background: #333;
}
.navbar .el-dialog__body .el-radio-group > .is-checked {
  border: 1px solid #ffc200;
}
.navbar .el-dialog__body .el-radio {
  width: 100%;
  background: #fbfbfa;
  border: 1px solid #e5e4e4;
  border-radius: 4px;
  padding: 14px 22px;
  margin-top: 20px;
}
.navbar .el-badge__content.is-fixed {
  top: 24px;
  right: 2px;
  width: 18px;
  height: 18px;
  font-size: 10px;
  line-height: 16px;
  font-size: 10px;
  border-radius: 50%;
  padding: 0;
}
.navbar .badgeW .el-badge__content.is-fixed {
  width: 30px;
  border-radius: 20px;
}
.el-icon-arrow-down {
  background: url(data:image/png;base64,) no-repeat 50% 50%;
  background-size: contain;
  width: 8px;
  height: 8px;
  -webkit-transform: rotate(0eg);
          transform: rotate(0eg);
  margin-left: 16px;
  position: absolute;
  right: 16px;
  top: 12px;
}
.el-icon-arrow-down:before {
  content: "";
}
.userInfo {
  background: #fff;
  position: absolute;
  top: 0px;
  left: 0;
  z-index: 99;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.14);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.14);
  width: 100%;
  border-radius: 4px;
  line-height: 32px;
  padding: 0 0 5px;
  height: 105px;
}
.userInfo .userList {
  width: 95%;
  padding-left: 5px;
}
.userInfo p {
  cursor: pointer;
  height: 32px;
  line-height: 32px;
  padding: 0 5px 0 7px;
}
.userInfo p i {
  margin-left: 10px;
  vertical-align: middle;
  margin-top: 4px;
  float: right;
}
.userInfo p:hover {
  background: #f6f1e1;
}
.msgTip {
  color: #419eff;
  padding: 0 5px;
}