/* Variables */

.avatar-uploader .el-upload {
  border-radius: 4px !important;
  background: #FCFCFC;
}

.avatar-uploader .avatar-uploader-icon {
  background: #FCFCFC;
}

.avatar-uploader .el-icon-plus:before {
  content: "Upload Picture" !important;
  font-size: 12px;
  color: #000;
}

.el-dialog {
  border-radius: 8px;
}

.el-dialog__header {
  background: #FBFBFA;
  border-radius: 8px 8px 0 0;
  border: none;
}

/* Mixins */
.login-ruiji .el-form-item {
  border: 1px solid red;
}
.login-ruiji .add-distance {
  margin-top: 68px;
}

.el-radio .el-radio__label {
  color: #333;
}

.el-radio__inner::after {
  background-color: #333;
}

.el-form-item__label {
  color: #333;
}

.el-input__inner {
  border-color: #E5E4E4;
}

.el-input__inner::-webkit-input-placeholder,
.el-input__inner::-moz-placeholder,
.el-input__inner::-ms-input-placeholder {
  color: #BDBDBD;
}

.dashboard-container {
  margin: 0 !important;
  padding: 20px !important;
}
.dashboard-container .el-table .cell .el-image > img {
  width: 40px;
  border-radius: 5px;
}

.el-pagination .el-pagination__total {
  font-size: 14px;
  font-family: PingFangSC, PingFangSC-Regular;
  font-weight: 400;
  color: #333333;
}
.el-pagination .el-pagination__sizes .el-input__inner {
  font-size: 14px;
  font-family: Helvetica;
  text-align: left;
  color: #333333;
}
.el-pagination .el-pager {
  font-size: 14px;
  font-family: MicrosoftYaHei;
  font-weight: 400 !important;
}
.el-pagination .el-pagination__jump {
  font-size: 14px;
  font-family: PingFangSC, PingFangSC-Regular;
  font-weight: 400;
  color: #333333;
}
.el-pagination .el-pagination__jump .el-pagination__editor.el-input {
  font-family: Helvetica;
}

.el-icon-circle-close {
  color: white;
}

.dashboard-container .container .el-table tbody tr td .cell button span {
  padding: 0 10px;
}

.dashboard-container .container .el-table tbody tr td .cell button:first-child span {
  padding-left: 10px;
}

.el-button--primary {
  color: #333333;
  background: #FFC200;
  border-color: #FFC200;
}

.el-button--primary:hover {
  background: #fdd24c;
  border-color: #fdd24c;
  color: #333333;
}

.el-button--primary:active {
  background: #efb602;
  border-color: #efb602;
  color: #333333;
}

.el-button--text {
  font-weight: 400 !important;
  font-size: 13px !important;
}

.el-table td {
  font-size: 13px !important;
}

.el-table .cell,
.el-table th div,
.el-table--border td:first-child .cell,
.el-table--border th:first-child .cell {
  padding-left: 30px !important;
  padding-right: 0 !important;
}

.el-table-column--selection .cell {
  padding-left: 12px !important;
  padding-right: 0 !important;
  text-overflow: clip !important;
}

.el-table-column--selection .cell .el-checkbox__inner {
  width: 13px;
}